import React, { useState } from 'react'; // Correctly import useState and useEffect
import '/new-project/tensorgo/src/styles.css';
import HelpCrunch from '/new-project/tensorgo/src/components/HelpCrunch';
import CustomerServiceForm from './CustomerServiceForm';
import DisplayRequests from './CustomerServiceRequest';
import { useAuth0 } from '@auth0/auth0-react';
import { useNavigate } from 'react-router-dom';

const MainPage = () => {
  const { isAuthenticated, loginWithRedirect } = useAuth0();
  const navigate = useNavigate();

  // State to manage the list of customer service requests
  const [requests, setRequests] = useState([]);

  // Handle new request submission
  const handleNewRequest = (newRequest) => {
    console.log("New request submitted:",newRequest);
    setRequests((prevRequests) => [...prevRequests, newRequest]);
  };

  // Handle authentication and redirect after login
  // useEffect(() => {
  //   if (!isAuthenticated) {
  //     loginWithRedirect();
  //   } else {
  //     navigate('/MainPage'); // Navigate to MainPage after login
  //   }
  // }, [isAuthenticated, loginWithRedirect, navigate]);

  return (
    <div className="App">
      <h1>Customer Service Platform</h1>
      <main>
        <CustomerServiceForm onSubmit={handleNewRequest} /> {/* Pass down handleNewRequest as a prop */}
        <DisplayRequests requests={requests} /> {/* Pass down requests as a prop */}
        <HelpCrunch/>
      </main>
    </div>
  );
};

export default MainPage;
